//
//base
//
@font-face {
  font-family: "iconfont-base";
  src: url('data:application/octet-stream;base64,') format('truetype');
}

.iconfont-base {
  font-family: "iconfont-base" !important;
  @include iconfont-mixin;

  &.icon-circle:before {
    content: "\EA01"
  }

  &.icon-download:before {
    content: "\EA02"
  }

  &.icon-info:before {
    content: "\EA03"
  }

  &.icon-safe-success:before {
    content: "\EA04"
  }

  &.icon-safe-warn:before {
    content: "\EA05"
  }

  &.icon-success:before {
    content: "\EA06"
  }

  &.icon-success-circle:before {
    content: "\EA07"
  }

  &.icon-success-no-circle:before {
    content: "\EA08"
  }

  &.icon-waiting:before {
    content: "\EA09"
  }

  &.icon-waiting-circle:before {
    content: "\EA0A"
  }

  &.icon-warn:before {
    content: "\EA0B"
  }

  &.icon-info-circle:before {
    content: "\EA0C"
  }

  &.icon-cancel:before {
    content: "\EA0D"
  }

  &.icon-search:before {
    content: "\EA0E"
  }

  &.icon-clear:before {
    content: "\EA0F"
  }
}


